import { proxyRefs, reactive } from "@vue/reactivity";
import { hasOwnKey, isFunction, isObject } from "@vue/shared";

export const createComponentInstance = (vnode) => {
  const instance = {
    /** 组件本身的数据 */
    data: null,
    /** 组件本身的虚拟节点 */
    vnode,
    /** render 产生的子节点 */
    subTree: null,
    /**  是否挂载过 */
    isMounted: false,
    /** render 函数 */
    render: null,
    /** 组件中用户写的 */
    propsOption: vnode.type.props || {},
    /** 用户接收的属性，创建虚拟节点的时候提供的 */
    props: {},
    /** 没有接收的属性 */
    attrs: {},
    /** 代理对象 */
    proxy: null,
    /** setup 返回的是对象时对这个属性赋值 */
    setupState: {},
  };

  return instance;
};

const initProps = (instance, rawProps) => {
  const props = {};
  const attrs = {};

  const options = instance.propsOption;

  if (rawProps) {
    for (let key in rawProps) {
      const value = rawProps[key];

      if (key in options) {
        props[key] = value;
      } else {
        attrs[key] = value;
      }
    }
  }

  /** 内部用的是浅响应式 */
  instance.props = reactive(props);
  /** 默认是非响应式的 */
  instance.attrs = attrs;
};

const publicProperties = {
  $attrs: (instance) => instance.attrs,
};

const instanceProxy = {
  get(target, key, receiver) {
    const { data, props, setupState } = target;
    if (setupState && hasOwnKey(setupState, key)) {
      return setupState[key];
    } else if (data && hasOwnKey(data, key)) {
      return data[key];
    } else if (props && hasOwnKey(props, key)) {
      return props[key];
    }

    const getter = publicProperties[key];
    if (getter) {
      return getter(target);
    }

    return null;
  },
  set(target, key, value, receiver) {
    const { data, props, setupState } = target;
    if (setupState && hasOwnKey(setupState, key)) {
      setupState[key] = value;
    } else if (data && hasOwnKey(data, key)) {
      data[key] = value;
    } else if (props && hasOwnKey(props, key)) {
      console.warn("can`t update props");
      return false;
    }
    return true;
  },
};

export const setupComponent = (instance) => {
  const { type, props, children } = instance.vnode;
  const { data, render, setup } = type;

  initProps(instance, props);

  if (data) {
    /** data 必须为函数 */
    if (!isFunction(data)) {
      return console.warn("The data option must be function");
    }

    /** 给实例赋值 data */
    instance.data = reactive(data.call({}));
  }

  /** 存在 setup */
  if (setup) {
    const context = {
        emit: () => {},
        attrs: instance.attrs,
        slots: null
    }
    const setupResult = setup(instance.props, context);

    if (isFunction(setupResult)) {
      /** setup 返回值为函数 */
      instance.render = setupResult;
    } else if (isObject(setupResult)) {
      /** setup 返回值为对象 */
      instance.setupState = proxyRefs(setupResult);
    } else {
      console.warn("setup type error");
    }
  }

  instance.proxy = new Proxy(instance, instanceProxy);

  if (!instance.render) {
    if (render) {
      instance.render = render;
    } else {
      /** 模板编译 */
    }
  }
};
